import React from 'react';
import "./om_home.css";
import EndzoneNav from "../../components/navigators/endzone-nav/EndzoneNav";
import ContentBox from './options/content-box2';
import 'react-phone-number-input/style.css';

import MembersImage from '../org_management/images/members.jpg'
import PaymentImage from '../org_management/images/payment.jpg'
import ProfileImage from '../org_management/images/profile.jpg'

import { Alert } from '@mui/material';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';

class OMHome extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            alert: {
                style: {display: "none"},
                severity: "error",
                userMessage: ""
            }
        }
    }

    closeAlert() {
        this.setState ({alert: {
            style: {display: "none"}}
        });
    }

    SuccessMessage(msg) {
        this.setState ({alert: {
            style: {display: "block"},
            severity: "success",
            userMessage: msg}
        });
    }

    FailureMessage(msg) {
        this.setState ({alert: {
            style: {display: "block"},
            severity: "error",
            userMessage: msg}
        });
    }


    render() {
        return(
            <div className="page-display">
                <EndzoneNav links={[{title: "Return to Hub", link: "/endzone/hub"},]}></EndzoneNav>
                <div className='alert-div' id="alert" style={this.state.alert.style}>
                    <Alert variant="filled" severity={this.state.alert.severity}
                            action={
                                <IconButton
                                    aria-label="close"
                                    color="inherit"
                                    size="large"
                                    onClick={() => {
                                        this.closeAlert();
                                    }}
                                >
                                    <CloseIcon fontSize="inherit"/>
                                </IconButton>
                            }>
                            {this.state.alert.userMessage}
                    </Alert>
                </div>
                <div className="body-container">
                    
                    <div className="content-box-option" onClick={() => window.location.href = "/endzone/org/profile"}>
                        <ContentBox
                            title = "Org Profile"
                            image={ProfileImage}>
                        </ContentBox>
                    </div>

                    <div className="content-box-option" onClick={() => window.location.href = "/endzone/org/members"}>
                        <ContentBox
                            title = "Members"
                            image={MembersImage}>
                        </ContentBox>
                    </div>

                    <div className="content-box-option" onClick={() => window.location.href = "/endzone/org/payment"}>
                        <ContentBox
                            title = "Payment"
                            image={PaymentImage}>
                        </ContentBox>
                    </div>
                </div>
            </div>
        );
    }
}

export default OMHome;